@use '../../style/variable.scss';

.kit-type-flex {
    .wkit-import-type {
        position: relative;
        display: flex;
        flex-direction: column;
        border: 1px solid #E6E6F3;
        border-radius: 10px;
        padding: 20px;
        width: 35%;
        margin-top: 20px;
        cursor: pointer;
        transition: variable.$transition;

        &:hover,
        &.wkit-selected-type {
            border-color: variable.$Wdkit_blue_normal;
            background-color: #03048303;

            .wkit-import-opt-title {
                color: variable.$Wdkit_blue_normal;
            }
        }

        &.wkit-selected-type .wkit-import-type-select {
            display: flex;
        }
    }

    .wkit-import-type-select {
        display: none;
        background-color: variable.$Wdkit_blue_normal;
        position: absolute;
        top: -10px;
        right: -10px;
        border-radius: 50%;
        width: 20px;
        height: 20px;
        flex-direction: row;
        align-items: center;
        justify-content: center;

        svg {
            width: 12px;
        }
    }

    .wkit-import-option {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 20px;
        width: 100%;

        svg {
            width: 35px;
            height: 35px;
        }
    }

    .wkit-import-opt-title {
        font-size: 20px;
        font-weight: 500;
        color: #020202;
        white-space: nowrap;
    }

    .wkit-import-opt-desc {
        font-size: 12px;
        font-weight: 400;
        color: #737373;
        white-space: nowrap;
    }

    .wkit-show-global {
        display: flex;
        flex-direction: column;
        gap: 15px;
        width: 100%;

        .wkit-show-global-color {
            display: flex;
            flex-direction: column;
            gap: 25px;
            border: 1px solid #E6E6F3;
            border-radius: 10px;
            padding: 15px;
        }

        .wkit-show-global-header {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            gap: 15px;
        }

        .wkit-show-global-txt {
            display: flex;
            flex-direction: column;
            gap: 10px;
        }

        .wkit-show-global-title {
            font-size: 20px;
            font-weight: 600;
            color: #020202;
            text-transform: capitalize;
        }

        .wkit-show-global-desc {
            font-size: 14px;
            font-weight: 400;
            color: #737373;
        }

        .wkit-global-submit-btn {
            font-size: 14px;
            font-weight: 400;
            min-width: 265px;
            max-height: 35px;
        }

        .wkit-global-added {
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 10px;
            padding: 10px;
            font-size: 14px;
            background: #E6F6E8;
            border-radius: variable.$radius_small;
            color: #00A31B;

            .wkit-global-success-svg {
                min-height: 20px;
                min-width: 20px;
            }

        }


        //card design
        .wkit-show-global-list {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            align-items: center;
            gap: 15px;
        }
    }
}

.wkit-show-global-color.wkit-skeleton {

    .wkit-show-global-title,
    .wkit-show-global-desc,
    .wkit-show-global-card,
    .wkit-outer-btn-class.wkit-global-submit-btn {
        background: variable.$skeleton_bg;
        animation: variable.$animation;
        width: fit-content;
        border-radius: variable.$radius_small;
        color: transparent;
        pointer-events: none;
        border: none !important;

        img,
        input,
        svg,
        .wkit-global-card-dot,
        .wkit-global-card-val {
            visibility: hidden;
        }

        span,
        label {
            color: transparent !important;
        }
    }
}

.wkit-global-step-notice {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;

    .wkit-loading-button {
        width: 200px;
        pointer-events: none;
    }

    .wkit-update-preset-btn {
        display: flex;
        justify-content: center;
        width: 100px;
        float: right;
        min-width: max-content;
    }

    //loader 
    .wkit-update-loader {
        display: flex;
        justify-content: center;
        align-items: center;

        .wkit-update-loader-circle {
            border: 3px solid #f3f3f3;
            border-radius: 50%;
            border-top: 3px solid variable.$Wdkit_blue_normal;
            width: 18px;
            height: 18px;
            animation: spin 1s linear infinite;
        }
    }

    .wkit-remove-plugin-notice {
        display: flex;
        gap: 5px;
        border-left: 3px solid #FFCD38;
        padding: 5px 0 5px 10px;
        font-size: 12px;

        .wkit-bold-text {
            font-weight: 500;
            color: #020202;
        }

        .wkit-notice-text {
            color: #737373;
        }

    }
}

.wkit-show-global-card {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #E6E6F3;
    background-color: transparent;
    padding: 5px 10px;
    border-radius: 6px;
    cursor: pointer;
    transition: 0.3s linear;

    &:disabled {
        cursor: not-allowed;
        opacity: 0.5;
    }

    .wkit-global-selected-tick {
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        top: -8px;
        right: -6px;
        background-color: variable.$Wdkit_blue_normal;
        border-radius: 50%;
        padding: 2px;
    }

    &:not(:disabled) {

        &:hover,
        &.wkit-selected_value {
            border: 1px solid variable.$Wdkit_blue_normal;
            color: variable.$Wdkit_blue_normal;
        }

        &:hover .wkit-global-card-tooltip {
            display: flex;
        }
    }

    .wkit-global-typo-fontfamily {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 5px;
        justify-content: flex-start;
        width: 100%;
        margin-bottom: 15px;

        span {
            font-size: 14px;
            font-weight: 500;
            color: #1A1A1ACC;
        }
    }

    .wkit-global-card-typo {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
        width: 100%;
    }

    .wkit-typo-icon {
        width: 25px;
        height: 25px;
    }

    .wkit-typo-detail {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 3px;

        span {
            margin-left: 3px;
            color: #1A1A1ACC;
        }
    }
}

.wkit-global-card-tooltip {
    position: absolute;
    display: none;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: #000;
    text-transform: capitalize;
    color: #fff;
    padding: 8px 10px;
    border-radius: 6px;
    width: max-content;
    top: -37px;

    &::before {
        content: '';
        background: #000;
        padding: 5px;
        position: absolute;
        bottom: -4px;
        transform: rotate(45deg);
        border-radius: 2px;
    }
}

.wkit-global-card-content {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 7px;

}

.wkit-select-global-dpr {
    position: relative;
    width: 150px;

    .wkit-select-global-header {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        gap: 10px;
        border: 1px solid variable.$Wdkit_blue_normal;
        color: variable.$Wdkit_blue_normal;
        border-radius: 6px;
        padding: 5px 5px 5px 10px;
        cursor: pointer;

        svg {}
    }

    .wkit-global-card-dot {}

    .wkit-global-card-val {}

    .wkit-select-global-body {
        display: flex;
        flex-direction: column;
        border: 1px solid;
        position: absolute;
        top: 33px;
        width: 100%;
        max-height: 130px;
        overflow: auto;
        background: #fff;
        z-index: 9;
        padding: 5px;
        border-radius: 6px;

        &::-webkit-scrollbar {
            width: 5px;
        }

        /* Handle */
        &::-webkit-scrollbar-thumb {
            background: #C1BFBF;
            border-radius: 25px;
        }

        /* Track */
        &::-webkit-scrollbar-track {
            background: #F8F8F8;
            border-radius: 25px;
        }

        /* Handle on hover */
        &::-webkit-scrollbar-thumb:hover {
            background: #B0AFAF;
        }
    }

    .wkit-select-body-cn {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 10px;
        padding: 5px;
        transition: 0.3s linear;
        border-radius: 6px;
        width: 100%;
        cursor: pointer;

        &:hover {
            background: #E6E6F3;
        }
    }

}

.wkit-global-card-dot {
    width: 20px;
    height: 20px;
    display: block;
    border-radius: 50%;

    &.wkit-circle-border {
        border: 1px solid #e6e6f380;
    }
}

.wkit-global-card-val {}

.wkit-global-card-icon {}

.wkit-inner-popup-body {
    padding: 10px 5px;
    display: flex;
    flex-direction: column;

    hr {
        width: 100%;
        height: 2px;
        border: 1px solid #E6E6F3;
        border-top: 0px;
        margin: 15px 0px;
    }

    .wkit-global-inner-popup {
        display: flex;
        flex-direction: column;
        background-color: #fff;
        width: 60%;
        height: auto;
        padding: 10px;
        border-radius: 10px;
    }

    .wkit-inner-popup-close {
        cursor: pointer;
    }

    .wkit-selected-global-value {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        .wkit-selected-title {
            text-transform: capitalize;
            font-size: 14px;
            font-weight: 600;
            color: #020202;
        }
    }

    .wkit-global-color-options {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        gap: 10px;
    }

    .wkit-global-typography-options {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
        width: 100%;
    }
}

.wkit-inner-popup-footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;

    .wkit-popup-submit-btn {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 10px;
        padding: 10px;
        border-radius: 6px;
        background-color: #F9F9FB;
        width: max-content;

        .wkit-global-card-val,
        .wkit-global-card-dot {
            color: variable.$Wdkit_blue_normal;
        }
    }

    .wkit-global-replace-val {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 5px;
    }

    .wkit-replace-color-box {
        width: 20px;
        height: 20px;
        display: block;
        border-radius: 50%;
    }

    .wkit-replace-color-txt {
        font-size: 12px;
        font-weight: 500;
    }

    .wkit-replace-global-txt {
        font-size: 12px;
        font-weight: 500;
        color: #999999;
    }

    .wkit-global-site-val {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 5px;
    }

    .wkit-selected-color-box {
        width: 20px;
        height: 20px;
        display: block;
        border-radius: 50%;
    }

    .wkit-selected-color-txt {
        font-size: 12px;
        font-weight: 500;
    }
}

@media(max-width:1200px) {

    .wkit-popup-body .kit-type-flex {
        .wkit-import-type {
            padding: 15px;
            width: 47%;
        }

        .wkit-show-global .wkit-show-global-header {
            flex-direction: column;
            align-items: unset;
        }

        .wkit-import-opt-title {
            font-size: 18px;
        }

        .wkit-import-option {
            gap: 15px;

            svg {
                width: 32px;
                height: 32px;
            }
        }
    }

    .wkit-inner-popup-body .wkit-global-typography-options {
        grid-template-columns: repeat(2, 1fr);
    }

}

@media(max-width:768px) {
    .wkit-global-step-notice {
        flex-direction: column;
        align-items: flex-end;
    }
}

@media(max-width:576px) {
    .wkit-popup-body .kit-type-flex .wkit-import-type {
        width: 95%;
    }

    .kit-type-flex .wkit-show-global .wkit-global-submit-btn {
        min-width: unset;
        font-size: 12px;
        padding: 8px;
    }

    .wkit-inner-popup-footer {
        flex-direction: column;
    }


    .wkit-inner-popup-footer {
        flex-direction: column;
        align-items: flex-end;
        gap: 10px;

        .wkit-global-replace-data {
            width: 100%;

            .wkit-popup-submit-btn {
                width: 100%;
                justify-content: center;
            }
        }
    }

    .wkit-inner-popup-body .wkit-global-typography-options {
        grid-template-columns: repeat(1, 1fr);

        .wkit-show-global-card .wkit-global-card-typo {
            grid-template-columns: repeat(4, 1fr);
        }

    }

}

@media(max-width:782px) {

    .wkit-popup-body .kit-type-flex {
        .wkit-import-opt-desc {
            white-space: nowrap;
        }

        .wkit-import-opt-title {
            white-space: nowrap;
        }
    }
}